section{padding:50px 0;min-height:300px}section.jumboimage{position:relative;text-align:left;color:#fff;padding:0;margin:0 auto;background:-webkit-gradient(linear,left bottom,left top,from(#000),color-stop(50%,#333));background:linear-gradient(0deg,#000,#333 50%)}section .cover-image{height:50vh;width:100%;opacity:.6;max-height:300px}section .cover-image>img{-o-object-fit:cover!important;object-fit:cover!important;-o-object-position:0 50%!important;object-position:0 50%!important}section .cover-over{position:absolute;top:2em}section hr{border-color:#f44336;border-width:3px;max-width:50px}section hr.light{border-color:#fff}section .section-heading{font-family:SFMono-Regular,Menlo,Monaco,Consolas,Liberation Mono,Courier New,monospace}section .slide-left[data-emergence=visible]{opacity:1;-webkit-animation:slideInLeft 1s;animation:slideInLeft 1s}section .slide-left[data-emergence=hidden]{opacity:0}section .slide-right[data-emergence=visible]{opacity:1;-webkit-animation:slideInRight 1s;animation:slideInRight 1s}section .slide-right[data-emergence=hidden]{opacity:0}